import gql from 'graphql-tag'
import { FRAGMENT_TIMELOG } from '../../shared/graphql/fragments'

export const QUERY_TIMELOGS_WITH_TASK = gql`
	query QueryDailyTimelogs(
		$where: TimelogWhereInput!
		$orderBy: TimelogOrderByInput
	) {
		timelogs(where: $where, orderBy: $orderBy) {
			...timelog
			task {
				id
				name
			}
		}
	}
	${FRAGMENT_TIMELOG}
`
export const QUERY_TIMELOGS = gql`
	query QueryTimelogs(
		$where: TimelogWhereInput!
		$orderBy: TimelogOrderByInput
	) {
		timelogs(where: $where, orderBy: $orderBy) {
			...timelog
			task {
				id
				name
			}
		}
	}
	${FRAGMENT_TIMELOG}
`

export const QUERY_TASKS = gql`
	query QueryTasks($where: TaskWhereInput!) {
		tasks(where: $where) {
			id
			name
		}
	}
`
